  /*  Toggle Switch  */
  .toggleSwitch span span {
    display: none;
  }
  
  .toggleSwitch {
    display: inline-block;
    height: 22px;
    position: relative;
    overflow: visible;
    padding: 0;
    cursor: pointer;
    width: 40px;
    margin-bottom: 0px;
  }
  
  .toggleSwitch * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  
  .toggleSwitch label,
  .toggleSwitch>span {
    line-height: 20px;
    height: 20px;
    vertical-align: middle;
  }
  
  .toggleSwitch input:focus~a,
  .toggleSwitch input:focus+label {
    outline: none;
  }
  
  .toggleSwitch label {
    position: relative;
    z-index: 3;
    display: block;
    width: 100%;
  }
  
  .toggleSwitch input {
    position: absolute;
    opacity: 0;
    z-index: 5;
  }
  
  .toggleSwitch>span {
    position: absolute;
    left: -40px;
    width: 100%;
    margin: 0;
    padding-right: 40px;
    text-align: left;
    white-space: nowrap;
  }
  
  .toggleSwitch>span span {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    display: block;
    width: 50%;
    margin-left: 50px;
    text-align: left;
    font-size: 13px;
    width: 100%;
    left: 15%;
    top: -2px;
    opacity: 0;
  }
  
  .toggleSwitch a {
    position: absolute;
    right: 50%;
    z-index: 4;
    display: block;
    height: 14px;
    padding: 0;
    left: 5px;
    width: 14px;
    top: 2px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 100%;
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  }
  
  .toggleSwitch>span span:first-of-type {
    color: #ccc;
    opacity: 1;
    left: 27%;
  }
  
  .toggleSwitch>span:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 40px;
    top: -2px;
    background-color: #fafafa;
    border: 1px solid #ccc;
    border-radius: 30px;
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
  }
  
  .toggleSwitch input:checked~a {
    border-color: #fff;
    left: 100%;
    margin-left: -18px;
    top: 2px;
  }
  
  .toggleSwitch input:checked~span:before {
    border-color: #467fcf;
    box-shadow: inset 0 0 0 30px #467fcf;
    background: #467fcf;
  }
  
  .toggleSwitch input:checked~span span:first-of-type {
    opacity: 0;
  }
  
  .toggleSwitch input:checked~span span:last-of-type {
    opacity: 1;
    color: #fff;
  }
  
