  .calendar-checkbox {
    position: absolute;
    right: 3px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    top: 10px;
    padding: 2px;
    top: 10px;
  }
  
  .calendar-checkbox input[type="checkbox"] {
    width: 0;
    height: 0;
    cursor: pointer;
  }
  
  .calendar-checkbox input[type="checkbox"]:after {
    content: "";
    position: absolute;
    width: 14px;
    height: 14px;
    left: 2px;
    top: 2px;
    border-radius: 50%;
    background: #fff;
  }
  
  .calendar-checkbox input[type="checkbox"]:checked:after {
    background: none;
  }
  
  .calendar-checkbox input[type="checkbox"]:checked:before {
    content: "";
    display: block;
    position: absolute;
    top: 2px;
    left: 6px;
    width: 6px;
    height: 11px;
    border: solid #fff;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
  }
  
  .flexible-modal .close-icon {
    background-color: #ffffffa8;
    border-radius: 0;
    right: 10px;
    top: 10px;
    color: #06172a;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
